export default {
	data() {
		return {
			data: [],
			fNoticeID: [],
			fNoticeTitle: [],
			content: [],
			fStartDate: [],
			fEndDate: [],
			date: '2018-12-29 00:00~2018-12-29 23:59',
			loadMoreText: "加载中...",
			showLoadMore: false,
			max: 0,
			state: 1
		}
	},
	onLoad() {
		this.initData();
	},
	onUnload() {
		this.max = 0,
			this.data = [],
			this.loadMoreText = "加载更多",
			this.showLoadMore = false;
	},
	onReachBottom() {
		if (this.state == 0) {
			this.loadMoreText = "没有更多数据了!"
			console.log('没有更多数据了')
			return;
		} else {
			this.showLoadMore = true;
			//this.setDate();
		}

	},
	onPullDownRefresh() {
		//this.initData();
	},
	methods: {
		initData() {
			setTimeout(() => {
				this.max = 0;
				this.fNoticeID.length = 0
				this.fNoticeTitle.length = 0
				this.content.length = 0
				this.fStartDate.length = 0
				this.fEndDate.length = 0
				this.loadMoreText = ''
				uni.stopPullDownRefresh();
			}, 300);
		},
		goDetail: function(id, title) {
			let detail = {
				id: id,
				title: title
			}
			uni.navigateTo({
				url: "notedata?detail=" + encodeURIComponent(JSON.stringify(detail))
			})
		},
	}
}
